.ship-player {
    position: absolute;
    bottom: 0px;
    left: 0px;
    height: 11.8%;
    width: 18.8%;
    animation-timing-function: cubic-bezier(0.12, 0.7, 0.44, 1.01); 
}

.block-item-can {
    position: absolute;
    height: 12.2%;
    width: 5.6%;
    background-image: url(../assets/images/stage1-game/block-can.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.block-item-can.is-touched {
    opacity: 0.0;
}

.block-item-can > div {
    width: 100%;
    height: 100%;
    background-image: url(../assets/images/stage1-game/block-can.png);
    background-repeat: no-repeat;
    background-size: contain;
}

.block-item-can.is-touched > div {
    opacity: 0.0;
    animation: collision-down 2s;
    animation-timing-function: ease-out;
    animation-iteration-count: 1;
}

.block-ship-1 {
    position: absolute;
    height: 7.6%;
    width: 9.7%;
    background-image: url(../assets/images/stage1-game/block-ship1.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.block-ship-2 {
    position: absolute;
    height: 12.3%;
    width: 4.7%;
    background-image: url(../assets/images/stage1-game/block-ship2.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.block-ship-3 {
    position: absolute;
    height: 9.2%;
    width: 10.3%;
    background-image: url(../assets/images/stage1-game/block-ship3.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.terminal {
    position: absolute;
    width: 38.9%;
    right: -38.9%;
    bottom: 0%;
    animation: terminalShow 2.1s forwards;
}

@keyframes terminalShow {
    0% { right: -38.9%; }
    100% { right: 0%; }
}

@keyframes collision-down {
    0% { transform: translate(0px, 0px);}
    100% { transform: translate(30vw, 70vh); }
}

@keyframes jump {
    0% { transform: translate(0px, 0px); }
    100% { transform: translate(0px, -3vh); }
}

@keyframes shown {
    0% { opacity: 0.5; }
    100% { opacity: 1.0; }
}

@keyframes background-move {
    0% {
        transform: translateX(0); 
    }
    100% {
        transform: translateX(-5%); 
    } 
}

@keyframes add-50 {
    0% {
        opacity: 1.0;
        transform: translate(0px, 0px);
    }
    100% {
        opacity: 0.0;
        transform: translate(-3vh, -3vh);
    }
}

.game-foreground {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 198%;
    height: 100%;
    display: flex;
    z-index: -1;
    background-image: url(../assets/images/stage1-game/bg-loop.jpg);
    background-repeat: repeat-x;
    background-size: 50% 100%;
}